@font-face {
  font-family: "Arcade Adventure";
  src: url("../assets/fonts/Kanit-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Arcade Adventure Bold";
  src: url("../assets/fonts/Kanit-Bold.ttf") format("truetype");
}

:root {
  --font: "Arcade Adventure";
  --font-bold: "Arcade Adventure Bold";
  --green: #369064;
  --light-green: #6ecea0;
  --off-white: #fffcc1;
  --red: #e64d48;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  background-color: var(--green);
}

h1,
h2 {
  padding: 0 2.5rem;
  color: var(--red);
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  text-shadow: -2px -2px 0 var(--off-white), 2px -2px 0 var(--off-white),
    -2px 2px 0 var(--off-white), 2px 2px 0 var(--off-white),
    0 -2px 0 var(--off-white), 0 2px 0 var(--off-white),
    -2px 0 0 var(--off-white), 2px 0 0 var(--off-white);
  text-align: center;
  font-family: var(--font-bold);
}

.explanation-text {
  text-align: center;
  font-family: var(--font);
  color: var(--light-green);
  font-size: 1.2rem;
}

button {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  border: 0;
  font-size: 20px;
  font-weight: 700;
  align-items: center;
  padding: 1rem;
  background: transparent;
  border-radius: 10px;
  text-align: center;
  width: fit-content;
  height: fit-content;
  color: white;

  &.primary {
    width: 100%;
    background-color: var(--red);
    border-bottom: 5px solid #901814;
  }

  &.secondary {
    width: 100%;
    background-color: var(--green);
    border-color: var(--green);
    padding-top: 0.75rem;
  }

  .icon {
    height: 20px;
    color: white;
  }
}

main {
  padding: 0 1rem 1rem;
}

.pushable {
  width: 100%;
  position: relative;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  outline-offset: 4px;
  transition: filter 250ms;

  &:hover,
  &:focus {
    filter: brightness(110%);

    .front {
      transform: translateY(-8px);
      transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
    }

    .shadow {
      transform: translateY(6px);
      transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
    }
  }

  &:active {
    .front {
      transform: translateY(-4px);
      transition: transform 34ms;
    }

    .shadow {
      transform: translateY(1px);
      transition: transform 34ms;
    }
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }

  .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(4px);
    transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
  }

  .edge {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
  }

  .front {
    width: 100%;
    display: flex;
    gap: 0.5rem;
    justify-content: center;
    position: relative;
    padding: 1rem 0;
    border-radius: 12px;
    font-size: 1.25rem;
    color: white;
    will-change: transform;
    transform: translateY(-6px);
    transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
  }

  .icon {
    height: 20px;
    color: white;
  }

  &.red .edge {
    background: linear-gradient(
      to left,
      hsl(340deg 100% 16%) 0%,
      hsl(340deg 100% 32%) 8%,
      hsl(340deg 100% 32%) 92%,
      hsl(340deg 100% 16%) 100%
    );
  }

  &.red .front {
    background: #e64d48;
  }

  &.green .edge {
    background: linear-gradient(
      to left,
      hsl(150deg, 45%, 16%) 0%,
      hsl(150deg, 45%, 32%) 8%,
      hsl(150deg, 45%, 32%) 92%,
      hsl(150deg, 45%, 16%) 100%
    );
  }

  &.green .front {
    background: #369064;
  }

  &.light-green .edge {
    background: linear-gradient(
      to left,
      hsl(150, 80%, 16%) 0%,
      hsl(150, 80%, 32%) 8%,
      hsl(150, 80%, 32%) 92%,
      hsl(150, 80%, 16%) 100%
    );
  }

  &.light-green .front {
    background: #6ecea0;
  }
}

button {
  .front {
    display: flex;
    gap: .5rem;
  }
}